}

.feedback__agreement {
  display: flex;
  margin-bottom: 40px;
}

.feedback__checkbox {
  display: none;
}

.feedback__checkbox:checked + .feedback__castom-checkbox .feedback__check {
  display: block;
}

.feedback__castom-checkbox {
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  margin-right: 25px;
  border: 1px solid #565658;
  border-radius: 3px;
}

.feedback__check {
  display: none;
}

.feedback__description {
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.03em;
  cursor: default;
}

.feedback__btn {
  margin-bottom: 36px;
}

.feedback__note {
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.03em;
  cursor: default;
}

.feedback__note::before {
  content: "* ";
  color: #EF4D9C;
}

.feedback__ticket {
  position: absolute;
  background-color: #F9931F;
  width: 48px;
  color: #FFF;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.feedback__ticket_max {
  z-index: 51;
  top: 20%;
  left: -48px;
  height: 188px;
  border-radius: 3px 0% 0% 3px/3px 0% 0% 3px;
  filter: drop-shadow(-2px 0px 6px rgba(0, 0, 0, 0.2));
  transition: 300ms;
  transform: translateX(0);
}

.feedback__ticket_max.active {
  transform: translateX(calc(100% + 6px));
}

.feedback__ticket_max span {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
  writing-mode: vertical-lr;
  transform: rotate(180deg);
}

.feedback__ticket_min {
  z-index: 53;
  top: 50%;
  left: 0;
  height: 56px;
  border-radius: 0% 3px 3px 0%/0% 3px 3px 0%;
  transform: translateY(-50%);
}

.feedback__ticket_min img {
  display: block;
}

@media (min-width: 992px) {
